* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  background-color: #212121;
  height: 50px; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    font-size: 15px;
    font-weight: 300;
    color: #ffffff;
    text-decoration: none; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    font-size: 13px;
    font-weight: 400;
    color: #ffffff; }

  .subheader_call {
    font-size: 12px;
    font-weight: 300;
    color: #ffffff;
    text-align: right;
    margin-top: 19px; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    font-size: 19px;
    line-height: 12px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    color: #ffffff; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0px -3px 0 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    color: #ffffff;
    text-transform: uppercase; } }

.promo {
  min-height: 800px;
  background: url(../img/main_bg.png) center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  .promo_subheader {
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  .promo_descr {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    text-align: center;
    color: #ffffff;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0px -4px 0 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7, #4ce2ff);
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    color: #ffffff; } }
